import React, { Component } from 'react'

class CareerPage extends Component {

  static async getInitialProps({req, query}) {
    return {
      career_name: query.slug
    }
  }

  render() {
    const { career_name } = this.props;
    return (
      <Layout>
        <NavBar/>
        Career: { career_name }
      </Layout>
    )
  }
}

export default CareerPage